.backdrop {
  visibility: visible;
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: rgba(0, 0, 0, .2);
  transition: all .25s;
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(1);
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: scale(2);
}

.modal {
