.thankYou_wrapper {
  height: 80vh;
  justify-content: center;
  align-items: center;
  display: grid;
}

.thankYou_title {
  text-align: center;
  padding: 20px;
  font-size: 32px;
}

.thankYou_img {
  width: 100px;
  margin-left: auto;
  margin-right: auto;
}

.demo__data {
  text-align: center;
  justify-content: center;
  gap: 8px;
  font-size: 16px;
  display: grid;
}

.demo__data a {
  color: #fff;
  text-decoration: underline;
}

.mobile__menu.is-open {
  height: 100vh;
}

.mobile__navigation {
  display: none;
}

.mobile__navigation.is-open {
  display: block;
}

.mobile__menu .navigation__opened {
  display: none;
}

.mobile__menu.is-open .navigation__opened {
  display: block;
}

.mobile__menu.is-open .navigation__closed {
  display: none;
}

.mobile__navigation {
  width: 100%;
  height: 100%;
  background-color: #0d0e13;
}

.mobile__navigation-list {
  height: 400px;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: grid;
}

.mobile__navigation-item {
  color: #fff;
  padding-bottom: 30px;
  font-size: 30px;
  line-height: 1.23;
}

.mobile__navigation-item:not(:last-child) {
  border-bottom: 1px solid #54546b;
}

.header__links.mobile {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__links-item.openButton.mobile {
  max-width: 300px;
  width: 100%;
  color: #fff;
  background: linear-gradient(#50b03a 0%, #3a9126 100%), #3a9126;
  justify-content: center;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
}

.header__links-item.mobile {
  width: 144px;
  background: #4b4b5c;
}

.navigation__closed {
  width: 20px;
  height: 13px;
}

.mobile__menu {
  background-color: #000;
}

.mobile__menu-container {
  height: 64px;
  border-bottom: 1px solid #89bf19;
  justify-content: space-around;
  align-items: center;
  gap: 10px;
  padding: 0 10px;
  display: flex;
}

@media screen and (min-width: 1001px) {
  .mobile__menu-container {
    display: none;
  }
}

.mobile__button {
  max-width: max-content;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
}

.mobileButton {
  width: 150px;
  height: 40px;
  text-align: center;
  color: #fff;
  background: linear-gradient(#50b03a 0%, #3a9126 100%), #3a9126;
  border: 0;
  font-size: 14px;
}

.mobileIcon {
  width: 44px;
  height: 44px;
  background: #4b4b5c;
  border: 1px solid #fff;
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.mobileIcon:hover {
  border: 1px solid #89bf19;
}

